.promo-report {

  .base-table {
    margin-bottom: 0;
  }
  .promo-report-table {
    .base-table th {
      padding: 1% 1%
    }
    td.number {
      text-align: right
    }
    td.money {
      text-align: right
    }
    td.date {
      text-align: right
    }
    th.number {
      text-align: right
    }
    th.money {
      text-align: right
    }
    th.date {
      text-align: right
    }
  }

  .promo-report-filter-group {
    .radio-button-label {
      padding-right: 30px;

      div {
        white-space: nowrap;
        display: inline-block
      }

      .radio-button-text {
        padding-left: 5px;
      }}
      padding: 20px
    }

    .promo-report-total {
      padding-left: 20px;
      padding-right: 30px;
    }

    .promo-report-total-cell {
      background: white;
      height: 300px;
    }
    .promo-report-total-cell .total-cell {
      padding-top: 7px;
      padding-bottom: 7px;
    }

    .promo-report-total-cell .total-cell h3, h2, h4, span {
      display: inline-block;
    }

    .promo-tags {

      margin-right: 20px;
      padding: 20px;
    }
    .react-tags {
      padding: 6px 0 0 6px;
      border: 1px solid #E4E7F4;
      border-radius: 5px;
      min-height: 87px;
      /* shared font styles */
      font-size: 1em;
      line-height: 1.2;
      /* clicking anywhere will focus the input */
      cursor: text;
    }

    .react-tags.is-focused {
      border-color: #B1B1B1;
    }

    .react-tags__selected {
      display: inline;
    }

    .react-tags__selected-tag {
      display: inline-block;
      box-sizing: border-box;
      margin: 0 6px 6px 0;
      padding: 6px 8px;
      border: 1px solid #E4E7F4;
      border-radius: 5px;
      background: #F1F1F1;

      /* match the font styles */
      font-size: inherit;
      line-height: inherit;
    }

    .react-tags__selected-tag:after {
      content: '\2715';
      color: #AAA;
      margin-left: 8px;
    }

    .react-tags__selected-tag:hover,
    .react-tags__selected-tag:focus {
      border-color: #B1B1B1;
    }

    .react-tags__search {
      display: inline-block;

      /* match tag layout */
      padding: 7px 2px;
      margin-bottom: 6px;

      /* prevent autoresize overflowing the container */
      max-width: 100%;
    }

    @media screen and (min-width: 30em) {

      .react-tags__search {
        /* this will become the offsetParent for suggestions */
        position: relative;
      }

    }

    .react-tags__search input {
      /* prevent autoresize overflowing the container */
      max-width: 100%;

      /* remove styles and layout from this element */
      margin: 0;
      padding: 0;
      border: 0;
      outline: none;

      /* match the font styles */
      font-size: inherit;
      line-height: inherit;
    }

    .react-tags__search input::-ms-clear {
      display: none;
    }

    .react-tags__suggestions {
      position: absolute;
      top: 100%;
      left: 0;
      width: 100%;
    }

    @media screen and (min-width: 30em) {
      .react-tags__suggestions {
        width: 240px;
        z-index: 5;
      }
    }

    .react-tags__suggestions ul {
      margin: 4px -1px;
      padding: 0;
      list-style: none;
      background: white;
      border: 1px solid #D1D1D1;
      border-radius: 2px;
      box-shadow: 0 2px 6px rgba(0, 0, 0, 0.2);
    }

    .react-tags__suggestions li {
      border-bottom: 0 solid #ddd;
      padding: 6px 8px;
    }

    .react-tags__suggestions li mark {
      text-decoration: underline;
      background: none;
      font-weight: 600;
    }

    .react-tags__suggestions li:hover {
      cursor: pointer;
      background: #eee;
    }

    .react-tags__suggestions li.is-active {
      background: #b7cfe0;
    }

    .react-tags__suggestions li.is-disabled {
      opacity: 0.5;
      cursor: auto;
    }

    .promo-chart {
      position: relative;
    }
    .promo-chart .promo-chart-metric {
      position: absolute;
      top: 20px;
      right: 5px;
      z-index: 4;
    }

    .promo-chart .select__field {
      border: 0;
      font-size: 0.8em;
      border-radius: 3px;
      padding: 1px 1px;
      width: 100px;
      height: auto;
      margin: 0;
    }
  }